#displayCategoryProducts {
    width: 80vw;
    min-width: 900px;
    min-height: 100vh;
    height: auto;
    background: white;
    margin: auto;
    box-shadow: 15px 0 20px -10px rgb(165, 164, 164),-15px 0 20px -10px rgb(165, 164, 164);
    padding-bottom: 15px;
    padding-top: 30px;
}

#order {
    width: 90%;
    height: 100px;
    margin: auto;
    margin-bottom: 20px;
}

#otherInfo {
    font-size: 12px;
    text-align: justify;
    width: 90%;
    margin: auto;
    padding-bottom: 20px;
}

#selectQty{
    width: 50%;
    display: inline-block;
}

#addToCart{
    width: 50%;
    display: inline-block;
    vertical-align: top; /* TRES UTILE */
}

.displayProductButton{
    background: black;
    color: white;
    border: 1px solid white;
    border-radius: 5px;
    -webkit-transition: background 0.2s ease-out;
    -moz-transition: background 0.2s ease-out;
    -o-transition: background 0.2s ease-out;
    transition: background 0.2s ease-out;
}

.displayProductButton:hover{
    color: darkslategray;
    border-color: darkslategray;
    background: white;
    cursor: pointer;
}

#productPictureContainer {
    width: 50%;
    min-height: 100vh;
}

#mobileOrderBlock{
    display: none;
}

#productInfo {
    width: 50%;
    min-height: 100vh;
    margin-top: -100vh;
    margin-left: 50%;
}

#productName {
    font-size: 25px;
    margin-top: 0px;
}

#productDescription {
    text-align:justify;
    width: 90%;
    margin: auto;
}

#incipit {
    width: 90%;
    margin: auto;
}

#blocJardinPremier{
    width: 90%;
}

#preparation {
    font-size: 15px;
}

.lineAndSquare {
    width: 100%;
}

#lineAndLogo{
    width: 95%;
}

#seeAlso{
    width: 100%;
    height: 200px;
}

.productPicture {
    width: 80%;
    box-shadow: 1px 1px 10px rgb(165, 164, 164);
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
}

.fadeIn{
    opacity: 1;
}

/* ////////////////////////////////////////////// digital tablet layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {
    #mobileOrderBlock{
        display: block;
        margin-top: 10px;
        margin-bottom: -20px;
    }

    #displayCategoryProducts {
        width: 100vw;
        min-width: 100vw;
        min-height: 100vh;
        box-shadow: none;
    }

    #orderBlock{
        display: none;
    }

    #incipit, #otherInfo{
        overflow: hidden;
    }

    .productPicture {
        width: 45vw;
        min-width: 295px;
        max-width: 350px;
        margin-top: 30px;
    }

    #jardinPremierContainer {
        display: none;
    }
    
    #selectQty {
        width: 50%;
        height: 100px;
    }
    
    #addToCart {
        width: 50%;
        height: 100px;
    }
    
    .lineAndSquare {
        width: 100%;
    }
    
    #seeAlso{
        height: 130px;
    }   
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 600px) {

    #displayCategoryProducts {
        width: 100vw;
        min-width: 100vw;
        min-height: 100vh;
        box-shadow: none;
        padding-top: 15px;
    }

    #order {
        position: relative;
        width: 100vw;
        margin-left: 0vw;
        top: 0px;
        height: auto;
        font-size: 13px;
    }

    #productPictureContainer {
        width: 100vw;
        height: auto;
        min-height: auto;
        margin-bottom: 10px;
    }

    #productInfo {
        width: 100vw;
        margin-top: 0vh;
        margin-left: 0%;
    }

    #productName {
        font-size: 20px;
        margin-top: 0px;
    }

    #incipit {
        width: 90%;
        margin: auto;
        margin-top: 10px;
    }

    #preparation {
        font-size: 15px;
    }
    
    .lineAndSquare {
        width: 150vw;
        margin-left: -30vw;
        height: auto;
    }
    
    #lineAndLogo{
        width: 200vw;
        margin-left: -50vw;
        height: 5vh;
        overflow: hidden;
    }
    
    #seeAlso{
        width: 100%;
        height: 200px;
    }

    .productPicture {
        width: 90vw;
        min-width: 90vw;
        max-width: 90vw;
        margin-top: 0px;
    }
    
    #selectQty {
        width: 50%;
    }
    
    #addToCart {
        width: 50%;
    }
    /* #preparation {
        font-size: 13px;
        top: calc(45vw + 325px);
    } */

    #preparation {
        position: relative;
        width: 100vw;
        margin-left: 0%;
        top: 0px;
    }
}