#displayProductBasket, #displayProductBuyConfirmation {
    position: fixed;
    background: black;
    width: calc(100vw - 5px);
    height: auto;
    left: 0;
    bottom: 20vw;
    border-radius: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    z-index: 2;
}

#displayProductBuyConfirmation{
    outline: 1px solid #feeba5;
    outline-offset: -5px;
    color: #feeba5;
    text-align: center;
    opacity: 1;
}

.fadeIn{
    opacity: 1;
}

/* ////////////////////////////////////////////// tablet phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {
    #displayProductBasket, #displayProductBuyConfirmation{
        width: 100vw;
    }
}