#root {
    height: 0px;
  }

body {
    margin: 0;
    font: 0;
    padding: 0;
    background: black;
    overflow-y: auto;
    font-family: Georgia, sans-serif;
    overflow-x: hidden;
}

::-webkit-scrollbar {
    width: 5px;
    background: white;
}
  
::-webkit-scrollbar-track {
    box-shadow: inset 1 1 5px white; 
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    background: rgb(95, 95, 95); 
    border-radius: 5px;
}
  
::-webkit-scrollbar-thumb:hover {
    background: rgb(165, 164, 164); 
}

#headerWrapper{
    width: 100vw;
    height: 150px;
    background: black;
    text-align: center;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 5px solid #feeba5;
}

#header{
    width: 80vw;
    min-width: 900px;
    height: 100%;
    background: black;
    margin: auto;
}

#centralLogo{
    width: 150px;
    height: 150px;
    background: black;
    margin: auto;
    background-image: url("./images/logo_main.png");
    background-size: cover;
    background-repeat: no-repeat ;
    box-shadow: none;
    -webkit-transition: box-shadow 0.3s ease-out;
    -moz-transition: box-shadow 0.3s ease-out;
    -o-transition: box-shadow 0.3s ease-out;
    transition: box-shadow 0.3s ease-out;
    border-radius: 50%;
}

#centralLogo:hover{
    box-shadow: 15px 0 20px -10px rgb(165, 164, 164),-15px 0 20px -10px rgb(165, 164, 164);
}

#displayWrapper, #homePageWrapper {
    margin-top: 2px;
    width: 100vw;
    height: auto;
    min-height: 100vh;
    background: #fefee2;
    text-align: center;
    color: darkslategrey;
    margin-bottom: 2px;
}

#homePage {
    width: 80vw;
    min-width: 900px;
    height: auto;
    min-height: 100vh;
    background: #fefee2;
    margin: auto;
    box-shadow: 15px 0 20px -10px rgb(165, 164, 164),-15px 0 20px -10px rgb(165, 164, 164);
    padding-bottom: 15px;
    text-align: center;
}

#menuWrapper{
    width: 100vw;
    height: 64px;
    min-width: 900px;
    background: black;
    text-align: center;
    border-bottom: 5px solid #feeba5;
    position: relative;
    z-index: 1;
}

#menu{
    width: 80vw;
    min-width: 900px;
    height: 100%;
    background: black;
    margin: auto;
}

.menuItem{
    text-align: center;
    width: 10%;
    height: 60px;
    box-sizing: border-box;
    background: #fefee2;
    font-size: 12px;
    border: 1px solid black;
    font-weight: normal;
    outline: 2px solid transparent;
    outline-offset: -5px;
    -webkit-transition: outline-color 0.3s ease-out;
    -moz-transition: outline-color 0.3s ease-out;
    -o-transition: outline-color 0.3s ease-out;
    transition: outline-color 0.3s ease-out;
}

.menuItem:hover {
    outline-color: black;
    background: beige;
    cursor: pointer;
}
  
.categoryTitle{
    width: 100%;
    font-size: 12px;
    height: auto;
    background: beige;
    padding-top: 5px;
    padding-bottom: 5px;
    box-sizing: border-box;
    border-bottom: 3px solid #feeba5;
}

#displayWrapper {
    margin-top: 2px;
    width: 100vw;
    height: auto;
    min-height: 100vh;
    background: #fefee2;
    text-align: center;
    color: darkslategrey;
    margin-bottom: 2px;
}

#displayCategory {
    width: 80vw;
    min-width: 900px;
    min-height: 100vh;
    height: auto;
    background: #fefee2;
    margin: auto;
    box-shadow: 15px 0 20px -10px rgb(165, 164, 164),-15px 0 20px -10px rgb(165, 164, 164);
    padding-bottom: 15px;
}

.displayTitle {
    width: 80%;
    height: auto;
    margin: auto;
    background: #fefee2;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
    border-bottom: 3px solid #feeba5;
}

#displayCategoryItems {
    width: 80vw;
    min-width: 900px;
    min-height: 100vh;
    height: auto;
    background: #fefee2;
    margin: auto;
    box-shadow: 15px 0 20px -10px rgb(165, 164, 164),-15px 0 20px -10px rgb(165, 164, 164);
    padding-bottom: 15px;
}

.category{
    width: 250px;
    height: auto;
    background: #fefee2;
    display: inline-block;
    margin-left: 5px;
    margin-top: 50px;
    margin-bottom: 5px;
    box-sizing: border-box;
    box-shadow: 1px 1px 10px lightslategrey;
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
}

.product:hover{
    cursor: pointer;
}

.category:hover{
    cursor: pointer;
    box-shadow: 1px 1px 10px darkslategray;
}

#footerWrapper {
    width: 100vw;
    min-width: 900px;
    height: 100px;
    background: black;
    text-align: center;
    border-top: 5px solid #feeba5;
}

#footer{
    width: 80vw;
    min-width: 900px;
    height: 100px;
    background: black;
    margin: auto;
}

/* ////////////////////////////////////////////// digital tablet layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {

    ::-webkit-scrollbar {
        display:none;
    }
   
    #headerWrapper{
        width: 100vw;
        height: 120px;
        padding-bottom: 10px;
        padding-top: 10px;
    }
    
    #header{
        width: 100vw;
        min-width: 100vw;
        height: 100%;
    }
    
    #centralLogo{
        width: 120px;
        height: 120px;
    }
    
    #homePageWrapper {
        height: 100vh;
        width: 100vw;
        min-width: 100vw;
        background: #fefee2;
        margin-top: 2px;
    }
    
    #homePage {
        width: 100vw;
        min-width: 100vw;
        box-shadow: none;
    }
    
    #menuWrapper {
        width: 30vw;
        height: auto;
        min-width: 340px;
        position: absolute;
        border-left: 5px solid #feeba5;
        border-right: 5px solid #feeba5;
        border-top: none;
        border-bottom: 5px solid #feeba5;
        box-sizing: border-box;
        margin-left: -340px;
        -webkit-transition: margin-left 0.15s ease-out;
        -moz-transition: margin-left 0.15s ease-out;
        -o-transition: margin-left 0.15s ease-out;
        transition: margin-left 0.15s ease-out;
    }
    
    #menu {
        width: 30vw;
        min-width: 330px;
        height: 100%;
        background: black;
        margin: auto;
    }
    
    .menuItem{
        width: 30vw;
        min-width: 326px;
        height: auto;
        padding-bottom: 15px;
        padding-top: 15px;
        box-sizing: border-box;
        font-size: 12px;
        display: block;
    }  
      
    .categoryTitle{
        width: 100%;
        font-size: 12px;
        padding-top: 3px;
        padding-bottom: 3px;
    }

    #displayWrapper {
        margin-top: 2px;
        width: 100vw;
    }
    
    #displayCategory {
        width: 100vw;
        min-width: 100vw;
        box-shadow: none;
    }

    .displayTitle {
        padding-top: 7px;
        padding-bottom: 7px;
        font-size: 15px;
    }
    
    #displayCategoryItems {
        width: 100vw;
        min-width: 100vw;
        min-height: 100vh;
        box-shadow: none;
    }
    
    #footerWrapper {
        min-width: 100vw;
    }
    
    #footer{
        width: 100vw;
        min-width: 100vw;
    }
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 600px) {

    #headerWrapper{
        height: 100px;
        padding-bottom: 5px;
        padding-top: 5px;
    }
    
    #centralLogo{
        width: 100px;
        height: 100px;
    }
    
    #menuWrapper {
        width: auto;
        min-width: auto;
        border-left: 3px solid #feeba5;
        border-right: 3px solid #feeba5;
        border-bottom: 3px solid #feeba5;
        margin-left: -70vw;
    }
    
    #menu {
        width: auto;
        min-width: auto;
    }
    
    .menuItem{
        width: 65vw;
        min-width: 65vw;
        padding-bottom: 12px;
        padding-top: 12px;
    }

    .category{
        margin-top: 10px;
    }
}