#topMenu {
    height: auto;
    width: auto;
    background: black;
    float: right;
    margin-top: -115px;
    color: #feeba5;
    font-size: 11px;
    text-align: left;
}

.topMenuItem {
    width: auto;
    height: auto;
    padding: 5px;
}

.topMenuIcon {
    padding-top: 15px;
    width: 15px;
    height: 11px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: none;
}

.topMenuIcon:hover{
    cursor: pointer;
}

.topMenuItem:hover {
    color: white;
    cursor: pointer
}

/* ////////////////////////////////////////////// tablet layout /////////////////////////////////////// */


@media screen and (max-width: 992px) {
    #topMenu {
        margin-top: -95px;
        margin-right: 20px;
    }
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////// */

@media screen and (max-width: 600px) {

    #topMenu {
        margin-top: -88px;
        margin-right: 5px;
        height: 90px;
        font-size: 10px;
    }
}