#displayBasket {
    width: 80vw;
    min-width: 900px;
    min-height: 100vh;
    height: auto;
    background: #fefee2;
    margin: auto;
    box-shadow: 15px 0 20px -10px rgb(165, 164, 164),-15px 0 20px -10px rgb(165, 164, 164);
    padding-bottom: 15px;
}

#columnHead {
    background: darkslategray;
    width: 80%;
    margin: auto;
    min-width: 900px;
}

#columnHead1{
    width: 42%;
}

#columnHead2, #columnHead3{
    width: 16%;
}

#columnHead4{
    width: 26%;
}

#basketLineContainer{
    height: auto;
    width: 80vw;
    min-width: 900px;
    color: white;
}

#emptyBasket{
    background: darkslategray;
    width: calc(80% + 5px);
    min-width: 900px;
    bottom: 20vh;
    position: fixed;
    color: white;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: auto;
}

#orderTotal{
    background: darkslategray;
    width: 80%;
    min-width: 900px;
    margin: auto;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
    height: auto;
}

/* ////////////////////////////////////////////// tablet phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {
    #displayBasket, #basketLineContainer, #emptyBasket, #orderTotal, #columnHead {
        width: 100vw;
        min-width: 100vw;
        font-size: 13px;
    }
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 600px) {
    #columnHead{
        padding-bottom: 10px;
        padding-top: 10px;
        font-size: 11.5px;
    }

    #columnHead1{
        width: 31%;
    }
    
    #columnHead2{
        width: 45%;
    }

    #columnHead3{
        width: 1%;
    }
    
    #columnHead4{
        width: 21%;
    }

    #orderTotal {
        font-size: 11.5px;
    }
}