.basketLine {
    width: 80%;
    min-width: 900px;
    margin: auto;
    color: white;
    background: black;
}

#basketLineMobileAddButton {
    display: none;
}

.basketlineLeft {
    width: 42%;
}

.basketlineUnder{
    width: 100vw;
}

.basketlineRight{
    width: 26%;
}

.basketlineCenter1, .basketlineCenter2 {
    width: 16%;
}

.basketLineButton, .basketLineButtonAdd {
    color: white;
    display : inline-block;
    height: 100%;
    border : 1px solid white;
    border-radius : 5px;
    padding-top: 5px;
    padding-bottom : 5px;
    -webkit-transition: background 0.3s ease-out;
    -moz-transition: background 0.3s ease-out;
    -o-transition: background 0.3s ease-out;
    transition: background 0.3s ease-out;
    -webkit-transition: border 0.3s ease-out;
    -moz-transition: border 0.3s ease-out;
    -o-transition: border 0.3s ease-out;
    transition: border 0.3s ease-out;
}

.bagSizeSetter{
    width: 30%;
}

.qtySetter{
    width: 20%;
}

.basketLineInfo {
    display : inline-block;
    height: 100%;
}

.basketLinePrice, .basketLineBagSize{
    width: 50%;
}

#basketLineAddButton{
    width: 35%
}

.closeOrDelete{
    display : inline-block;
    height: 100%;
    width: 12%;
    padding: 15px;
}

.basketLineButton:hover, .bagSizeSetter:hover{
    background: white;
    color: darkslategray;
    border-color: darkslategray;
    cursor: pointer;
}

#basketLineAddButton:hover, #basketLineMobileAddButton:hover{
    background: white;
    background: rgb(84, 182, 84);
    cursor: pointer;
}

.closeOrDelete:hover{
    color: darkslategray;
    cursor: pointer; 
}

.bagSizeSetter{
    width: 10%;
}

/* ////////////////////////////////////////////// tablet layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {
    .basketLine {
        width: 100vw;
        min-width: 100vw;
    }

    .basketlineRight{
        width: 28%;
    }
    
    .basketlineCenter2 {
        width: 14%;
    }

    #basketLineAddButton{
        width: 50%;
        padding: 10px;
    }

    .qtySetter{
        width: 30%;
    }

    .closeOrDelete{
        padding: 10px;
    }

    .bagSizeSetter{
        width: 15%;
    }
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 600px) {
    .basketLineButton, .basketLineButtonAdd{
        font-size: 15px;
    }

    .basketLine {
        font-size: 12px;
    }

    #basketLineMobileAddButton {
        display: inline-block;
        width: 30%;
    }

    .basketlineLeft {
        width: 40%;
        text-align: left;
        display: inline-flexbox;
    }

    .basketlineUnder {
        width : 100%;
    }
    
    .basketlineRight{
        width: 33%;
        display: inline-flexbox;
    }
    
    .basketlineCenter1 {
        width: 33%;
        display: inline-flexbox;
    }

    .basketlineCenter2 {
        width: 16%;
        display: inline-flexbox;
    }

    #basketLineAddButton {
        display: none;
    }

    .basketLinePrice, .basketLineBagSize{
        width: 100%;
    }

    .basketLineButton{
        text-align: center;
    }

    .closeOrDelete{
        width: 50%;
        padding: 10px;
    }

    .bagSizeSetter{
        width: 25%;
    }
}