.productContainer {
    width: 200px;
    height: auto;
    background: white;
    display: inline-block;
    margin-left: 10px;
    margin-top: 20px;
    box-sizing: border-box;
    opacity: 0;
    box-shadow: 1px 1px 10px rgb(165, 164, 164);
    -webkit-transition: opacity 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out;
    transition: opacity 0.3s ease-out;
    vertical-align: top;
}

.productContainer:hover {
    box-shadow: 1px 1px 10px black;
}

.thumbNailProduct {
    width: 100%;
    height: 200px;
    margin-bottom: -4px;
}

.buyButtonContainer {
    padding-top: 10px;
    padding-bottom: 10px;
}

.buyButton {
    width: 50%;
    height: auto;
    padding-top: 6px;
    padding-bottom: 6px;
    border-radius: 5px;
    border: 1px solid white;
    color: white;
    background: black;
    font-size: 12px;
    margin-left: 25%;
    -webkit-transition: background 0.3s ease-out;
    -moz-transition: background 0.3s ease-out;
    -o-transition: background 0.3s ease-out;
    transition: background 0.3s ease-out;
}

.buyButton:hover{
    color: darkslategray;
    border-color: darkslategray;
    background: white;
    cursor: pointer;
}

.thumbnailProductInfo{
    margin-top: 4px;
    width: 200px;
    min-height: 80px;
}

.fadeIn{
    opacity: 1;
}

/* ////////////////////////////////////////////// digital tablet layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {

    .productContainer {
        width: 150px;
    }

    .product {
        font-size: 13.5px;
        width: 150px;
        height: auto;
    }

    .thumbNailProduct {
        height: 150px;
    }

    .thumbnailProductInfo{
        width: 150px;
        min-height: auto;
    }
    
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 600px) {
    .productContainer {
        margin-left: 5px;
    }

    .product {
        padding-bottom: 10px;
    }

    .thumbNailProduct {
        height: 145px;
        width: 145px;
    }
}