.toggleMenu {
    position: absolute;
    display: none;
}

/* ////////////////////////////////////////////// digital tablet layout //////////////////////////////////////////////////// */

@media screen and (max-width: 992px) {

    .toggleMenu {
        width: 80px;
        height: 80px;
        background: black;
        margin-top: -110px;
        margin-left: 10px;
        border-radius: 50%;
        display: block;
        box-sizing: border-box;
        -webkit-transition: background 0.3s ease-out;
        -moz-transition: background 0.3s ease-out;
        -o-transition: background 0.3s ease-out;
        transition: background 0.3s ease-out;
    }

    .isToggled {
        border: 2px solid #feeba5;
        background: rgb(32, 32, 32);
    }

    .menuBar {
        width: 50%;
        background: #feeba5;
        height: 5px;
        border-radius: 2px;
        margin : auto;
        opacity : 1;
    }

    .toggleMenu:hover .menuBar{
        background: #c9b66f;
    }

    #menuBar1 {
        margin-top: 22.5px;
        -webkit-transition: opacity 0.3s ease-out;
        -moz-transition: opacity 0.3s ease-out;
        -o-transition: opacity 0.3s ease-out;
        transition: opacity 0.3s ease-out;
    }

    #menuBar2, #menuBar3 {
        margin-top: 7px;
        -webkit-transition: width 0.3s ease-out;
        -moz-transition: width 0.3s ease-out;
        -o-transition: width 0.3s ease-out;
        transition: width 0.3s ease-out;
    }

    .toggleMenu:hover {
        cursor: pointer;
    }
}

/* ////////////////////////////////////////////// mobile phone layout //////////////////////////////////////////////////// */

@media screen and (max-width: 600px) {

    .toggleMenu {
        width: 60px;
        height: 60px;
        margin-top: -85px;
        margin-left: 10px;
    }

    .menuBar {
        width: 30px;
        height: 4px;
    }

    #menuBar1 {
        margin-top: 16px;
    }

    #menuBar2, #menuBar3 {
        margin-top: 6px;
    }
}